body {
  margin: 40px;
  background-color: #E5E4E2;
}
* {
  box-sizing: border-box; 
}
.profile {
  padding: 10px;
  border-style: solid;
  border-width: 2px;
  border-color: black;
}
.name {
  background-color: #151B54;
  color: white;
  padding: 25px;
  text-align: center;
  font-weight: bolder;
  font-variant: small-caps;
}
.whatido {
  height: 250px;
  background-color: #95B9C7;
  text-align: center;
  padding: 10px;
}
.edu {
  border-style:solid;
  border-width: 2px;
  border-color: black;
  background-color: #95B9C7;
  border-radius:5px;
  padding: 5px;
  }
.works {
  height: 55 0px;
  background-color: #95B9C7;
  text-align: center;
  padding: 10px;
}
.image {
  padding: 8px;
}
.footer {
  background-color: #151B54;
  height: 150px;
  padding-top: 60px;
  word-spacing: 10px;
}
.myself {
  font-size: medium;
  border-style: groove;
  border-width: 5px;
}